<template>
  <!--该组件用于点击词条时,显示词条的详细信息-->
  <article class="showwords">
    <button class="close-btn" @click="close"></button>
    <div class="word-name">
      <h2 v-if="actioning===0" @dblclick="toUpdate">{{checkingword.name||''}}
      </h2>
      <input v-else v-model="name" placeholder="词条名" v-focus />
    </div>
    <div class="word-introduce">
      <textarea v-if="actioning===0" :value="checkingword.introduce||''"
        @dblclick="toUpdate" readonly placeholder="该词条暂时还没有介绍哦~"></textarea>
      <textarea v-else-if="actioning===1" v-model="introduce" class="inserting"
        placeholder="具体描述.."></textarea>
      <textarea v-else-if="actioning===2" v-model="introduce"
        placeholder="该词条暂时还没有介绍哦~"></textarea>
    </div>
    <span v-if="actioning===0"
      style="font-size:12px;display:block;padding-top:10px;color:#404040">*双击文本框以编辑*</span>
    <button v-else-if="actioning===1" class="confirm" @click="insert_h3">
      <span>添加</span>
    </button>
    <button v-else-if="actioning===2" class="confirm" @click="update_h3">
      <span>编辑</span>
    </button>
  </article>
</template>
<script>
export default {
  props: ['checkingword', 'h1', 'h2', 'actioning'],
  name: 'ShowWords',
  data () {
    return {
      // 添加词条用
      name: '',
      introduce: ''
    }
  },
  methods: {
    emitData (methodName, ...para) {
      // 该方法用于触发home组件上方法
      this.$emit(methodName, ...para)
    },
    close () {
      this.emitData('setActioning', null)
    },
    insert_h3 () {
      if (this.name.trim().length === 0) {
        alert('词条名不能为空')
        return
      }
      if (this.name.length > 80) {
        alert('词条名不能超过80')
        return
      }
      this.emitData('axiosGetData', 'insert/h3', { name: this.name, introduce: this.introduce, parents: this.h2, graparents: this.h1 }, function (res) {
        if (!res.data) {
          alert('添加词条失败了,请检查是否已存在同名录下的同名词条')
        }
        this.emitData('getFiles')
      }.bind(this))
    },
    toUpdate () {
      this.emitData('setActioning', 2)
    },
    update_h3 () {
      if (this.name.trim().length === 0) {
        alert('词条名不能为空')
        return
      }
      if (this.name.length > 80) {
        alert('词条名不能超过80')
        return
      }
      this.emitData('axiosGetData', 'update/h3', { id: this.checkingword.id, new_name: this.name, new_introduce: this.introduce }, function (res) {
        if (!res.data) {
          alert('编辑词条失败了,请检查是否已存在同名录下的同名词条')
          return
        }
        this.emitData('getFiles')
      }.bind(this))
    }
  },
  watch: {
    // 监听actioning值的改变初始化变量
    actioning (newval) {
      if (newval === 2) {
        this.name = this.checkingword.name
        this.introduce = this.checkingword.introduce
        return
      }
      this.name = ''
      this.introduce = ''
    }
  }
}
</script>
<style lang="stylus" scoped>
@media screen and (max-width: 700px) {
  .bodywords {
    display: none;
  }
}

.showwords {
  width: 400px;
  height: 300px;
  position: absolute;
  max-width: 40vmax;
  max-height: 30vmax;
  min-width: 300px;
  min-height: 200px;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  border: 2px solid #f5bea3;
  background: wheat;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 20px;
  text-align: center;

  .close-btn {
    position: absolute;
    right: 0px;
    top: 0px;
    font-size: 20px;
    width: 40px;
    height: 22px;
    border-bottom-left-radius: 50%;
    border: 1px solid #c0e0c0;
    border-top-width: 0px;
    border-right-width: 0px;
    cursor: pointer;

    &:after {
      content: '×';
      display: block;
      position: absolute;
      line-height: 16px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: all 0.1s;
    }

    &:active:after {
      top: 60%;
    }

    &:focus {
      outline: 0;
    }
  }

  .word-name {
    line-height: 24px;
    text-align: center;

    h2 {
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 16.5px;
    }

    input {
      text-align: center;
      border-width: 0;
      border-radius: 4px;
      outline-color: #00cccc;
      border: 1px solid #a0a0a0;
      height: 20px;
    }
  }

  .word-introduce {
    margin-top: 5px;
    height: 200px;
    text-align: center;

    textarea {
      width: 100%;
      height: 100%;
      resize: none;
      border-width: 0;
      border-radius: 8px;
      box-sizing: border-box;
      border: 1px solid #a0a0a0;
      outline-color: #0cc;
      outline-style: none;
      padding: 7px;
      padding-bottom: 0;
      word-break: break-all;

      &[readonly] {
        background: #ffffee;
      }

      &.inserting:focus {
        outline-style: auto;
      }
    }
  }

  .confirm {
    margin: 6px;
    width: 100px;
    height: 30px;
    font-size: 12px;
    border-radius: 2px;
    outline-color: #a0a0a0;
    border: 1px solid #a0a0a0;
    cursor: pointer;

    &:active span {
      position: relative;
      top: 2px;
    }
  }
}
</style>



